import React, { useEffect, useState } from 'react'
import { Form, Button,Row, Col, Container } from 'react-bootstrap'
import { Link } from 'react-router-dom'
import { LinkContainer } from 'react-router-bootstrap'

import Message from '../components/Message'
import Loader from '../components/Loader'
import FormContainer from '../components/FormContainer'
import { login } from '../actions/userActions'
import { useLoginStore, useLoginDispatch } from '../context'


function LoginScreen({history, location}) {
    // const [state, dispatch] = useUserLoginReducer()
    const state = useLoginStore()
    const dispatch = useLoginDispatch()
    const {userInfo, error} = state
    const [email, setEmail] = useState('')
    const [password, setPassword] = useState('')

    const redirect = location.search ? location.search.split('=')[1] : '/'


    useEffect(() => {
        if (userInfo) {
            history.push('/profile')
        }
    },[history, userInfo])

    const submitHandler = (e) => {
        e.preventDefault()
        login(email, password, dispatch)
    }


    return (
        <>
            <FormContainer>
                <h1></h1>
                
                {error && <Message variant='danger'>''</Message>}
                
                <Form onSubmit={submitHandler}>
                    <Form.Group controlId='email'>
                        <Form.Label className='mb-0'></Form.Label>
                        <Form.Control
                            type='email'
                            placeholder='e'
                            value={email}
                            onChange={
                                (e) => setEmail(e.target.value) 
                            }
                        ></Form.Control>
                    </Form.Group>


                    <Form.Group controlId='password'>
                        <Form.Label className='mb-0 mt-3'></Form.Label>
                        <Form.Control
                            type='password'
                            placeholder=''
                            value={password}
                            onChange={
                                (e) => setPassword(e.target.value)
                            }
                        ></Form.Control>
                    </Form.Group>

                    <Button type='submit' variant='primary' className='mt-2 mb-3'>
                        
                    </Button>

                </Form>

                <Row className="py-3">
                    <Col>
                        
                        <Link to={redirect ? `/register?redirect=${redirect}` : '/register'}>
                            {' '} 
                        </Link>
                    </Col>
                </Row>

            </FormContainer>

            <FormContainer>
                <Message variant='info'>
                    
                    <br></br>
                    <Link to={'/demo/profile'}>Demo</Link>
                </Message>
            </FormContainer>
        </>
    )
}

export default LoginScreen
