import React, { useEffect, useState } from 'react'
import { Button, Row, Col, Table, Container } from 'react-bootstrap'
import { LinkContainer } from 'react-router-bootstrap'
import axios from 'axios'
import { useLoginStore, useLoginDispatch , useProfileDispatch, useProfileStore, useShiftDispatch, useShiftStore} from '../context'
import { getProfile, login } from '../actions/userActions'
import { changeSubmitStatus, changePeriod } from '../actions/shiftActions'
import { SHIFT_REQUEST, SHIFT_SUCCESS } from '../constants/shiftConstants'


import Message from '../components/Message'
import Loader from '../components/Loader'
import { format, endOfDay, endOfMonth, subDays, startOfMonth, startOfDay } from 'date-fns'

function DemoProfileScreen({ history }) {
    //local
    const [shifts, setShifts] = useState([])

    //global
    const loginState = useLoginStore()
    const { userInfo } = loginState
    const loginDispatch = useLoginDispatch()
    const dispatch = useProfileDispatch()
    const profileState = useProfileStore()
    const { profile, loading: profileLoading } = profileState
    const shiftState = useShiftStore()
    const { period, deadline, isSubmitted, loading } = shiftState
    const shiftDispatch = useShiftDispatch()

    const getStringDate = (dt = new Date()) => {
        return dt.getFullYear() + '-' + ('0'+(dt.getMonth()+1)).slice(-2) + '-' + ('0'+dt.getDate()).slice(-2)
    }

    useEffect(() => {
        if (!userInfo) {
            const demoEmail = 'demo@email.com'
            const demoPassword = 'demo1234'
            login(demoEmail, demoPassword, loginDispatch)
        } else {
            getProfile(dispatch, userInfo)

            const config = {
                headers: {
                    'Content-type': 'application/json',
                    Authorization : `Bearer ${userInfo.token}`
                }
            }
            async function fetchShifts() {
                shiftDispatch({type: SHIFT_REQUEST})
                const { data } = await axios.get(
                    `/api/shifts/myshifts/`,
                    config
                )
                setShifts(data)
                
                shiftDispatch({type: SHIFT_SUCCESS})
            }
            fetchShifts()
        }
        
    }, [history, userInfo, isSubmitted, period])

//handler functions
    const isSubmittedToTrue = () => {
        changeSubmitStatus(shiftDispatch, true)
    }
    const isSubmittedToFalse = () => {
        changeSubmitStatus(shiftDispatch, false)
    }
    const deadlineToDefault = () => {
        changePeriod(shiftDispatch, subDays(endOfDay(endOfMonth(new Date())), 10))
    }
    const deadlineToStart = () => {
        changePeriod(shiftDispatch, startOfDay(startOfMonth(new Date())))
    }
    const deadlineToEnd = () => {
        changePeriod(shiftDispatch, endOfDay(endOfMonth(new Date())))
    }

    return (
        <div>
            <h1> {profile.name} </h1>
            <Row className='py-3'>
                

                {loading ? <Loader />
                    :
                    <Col md={5} sm={12} className='py-2'>
                        <h4></h4>
                        <Container className='border'>
                            <LinkContainer to='/demo/shift/submit' className='d-grid gap-2 my-3 p-1'>
                                <Button className='btn-lg' variant='outline-primary' disabled={isSubmitted || (new Date() > deadline)}> <p className='m-0'>{getStringDate(period[0])} ~ { getStringDate(period[1])}</p></Button>
                            </LinkContainer>
                            <p className='my-0 py-0'> {isSubmitted ?<i style={{color: 'blue'}}></i> : <i style={{color: 'red'}}></i>}</p>
                            <p className='my-0 py-0'> {String(format(deadline, "yyyy-MM-dd' 'HH:mm"))} </p>
                            {(!isSubmitted && (new Date() > deadline)) &&
                                <Message variant='danger'>  </Message>
                            }
                        </Container>

                        
                        <h4 className='mt-5'></h4>
                        {shifts.length === 0 ? <Message variant='info'></Message> :
                            <Table striped hover responsive className='table-sm border' >
                                <thead>
                                    <tr>
                                        <th>ID</th>
                                        <th>PERIOD</th>
                                        <th>STATUS</th>
                                        <th></th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {shifts.map((shift) => (
                                        <tr key={shift._id}>
                                            <td>{shift._id}</td>
                                            <td>{shift.period_start} {' '} ~ {' '}{shift.period_end}</td>
                                            <td>{shift.is_confirmed ? '' : ''}</td>
                                            <td>
                                                { shift.is_confirmed ? 
                                                    <LinkContainer to={`/demo/shifts/confirm/${shift._id}`}>
                                                        <Button className='btn-sm' variant='outline-primary'></Button>
                                                    </LinkContainer> 
                                                    :
                                                    <LinkContainer to={`/demo/shifts/update/${shift._id}`}>
                                                        <Button className='btn-sm' variant='outline-info'></Button>
                                                    </LinkContainer> 
                                                }
                                            </td>
                                        </tr>
                                    ))}
                                </tbody>
                            </Table>          
                        }
                        
                    </Col>
                }



                {profileLoading ? <Loader /> :
                    <Col md={7} sm={12} className='py-2'>

                    <Row>
                        <Col md ={10} sm={10} xs={10}>
                            <h4></h4>
                        </Col>
                        <Col md={2} sm={2} xs={2} className='m-0 px-0 pb-2 text-center'>
                            <LinkContainer to={'/demo/update/profile'}>
                                    <Button variant='outline-secondary'className='btn-sm'></Button>
                            </LinkContainer>
                        </Col>
                    </Row>
                        


                        <Table striped hover responsive size='sm' className='border'>
                            <thead>
                                <tr>
                                    <th>Key</th>
                                    <th>Value</th>
                                    <th>Remarks</th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr>
                                    <td>ID</td>
                                    <td>{profile._id}</td>
                                    <td></td>
                                </tr>
                                <tr>
                                    <td></td>
                                    <td>{profile.name}</td>
                                    <td></td>
                                </tr>
                                <tr>
                                    <td></td>
                                    <td>{profile.duty}</td>
                                    <td></td>
                                </tr>
                                <tr>
                                    <td></td>
                                    <td>{profile.section}</td>
                                    <td></td>
                                </tr>
                                <tr>
                                    <td></td>
                                    <td>{profile.employment_status}</td>
                                    <td></td>
                                </tr>
                                <tr>
                                    <td></td>
                                    <td>{profile.is_rookie ? '' : ''}</td>
                                    <td></td>
                                </tr>
                                <tr>
                                    <td></td>
                                    <td>{profile.is_open_staff ? '' : ''}</td>
                                    <td></td>
                                </tr>
                                <tr>
                                    <td>10</td>
                                    <td>{profile.is_pre_close_staff ? '' : ''}</td>
                                    <td></td>
                                </tr>
                                <tr>
                                    <td></td>
                                    <td>{profile.is_close_staff ? '' : ''}</td>
                                    <td></td>
                                </tr>
                                <tr>
                                    <td> </td>
                                    <td> {profile.start_default.substring(0,5)}</td>
                                    <td></td>
                                </tr>
                                <tr>
                                    <td> </td>
                                    <td> {profile.end_default.substring(0,5)}</td>
                                    <td></td>
                                </tr>
                                <tr>
                                    <td> </td>
                                    <td> {profile.desired_times_per_week}</td>
                                    <td></td>
                                </tr>
                                <tr>
                                    <td> </td>
                                    <td> {profile.desired_working_time}h</td>
                                    <td></td>
                                </tr>
                                <tr>
                                    <td> </td>
                                    <td> {profile.commute}</td>
                                    <td></td>
                                </tr>
                                <tr>
                                    <td> </td>
                                    <td> {profile.station}</td>
                                    <td> </td>
                                </tr>
                            
                            </tbody>
                        </Table>
                    </Col>
                } 


            </Row>

            <h4></h4>
            <Message variant='info'>
                
                <br></br>
                <br></br>
                Demo
                
            </Message>

            <Container className='border'>
                <Row className='m-3'>
                    <Col md={4} sm={4} xs={4}>
                        <Button onClick={isSubmittedToTrue} variant='outline-info' disabled={isSubmitted}></Button>
                    </Col>
                    <Col md={4} sm={4} xs={4}>
                        <Button onClick={isSubmittedToFalse} variant='outline-danger' disabled={!isSubmitted}></Button>
                    </Col>
                </Row>
                <Row className='m-3'>
                    <Col>
                        <Button onClick={deadlineToDefault} variant='outline-secondary'></Button>
                    </Col>
                    <Col>
                        <Button onClick={deadlineToEnd} variant='outline-secondary'></Button>
                    </Col>
                    <Col>
                        <Button onClick={deadlineToStart} variant='outline-secondary'></Button>
                    </Col>
                </Row>
            </Container>
        </div>
    )
}

export default DemoProfileScreen
